.declare_checkbox .custom-control-label::before, .declare_checkbox .custom-control-label::after {
	top: .55rem;
}
.declare_checkbox .custom-control-input:checked ~ .custom-control-label::before {
	border-color: #fb6500;
	background-color: #fb6500;
}
.payment_method {
	padding-left: 30px;
}
.payment_method .custom_check {
    position: relative;
    height: 100%;
    border: 2px solid #fb6500;
    padding: 30px;
    border-radius: 10px;
    display: block;
    color: #fb6500;
    margin-bottom: 10px;text-align: center;text-align: center;

font-size: 17px;
}
.payment_method .custom_check.active {
	color: #fff;
    background: #fb6500;
}

.payment_method .custom_check input {
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	position: absolute;
	opacity: 0;
}

@media screen and (max-width: 767px) {
    .about-video iframe {
        max-width: 1100px;
        height: 310px;
        width: 100%;
    }
}



.payment-button{
