.quote{
width: 100%;

}

.qoutes{

	position: relative;
	right:120px;
	top:10px;
}
.center-appr{
	position: relative;
	margin-bottom:40px;
	width:100%;
}

@media screen and (min-width: 500px) {
	.center-appr{
		padding-left : 100px;
	}
	
}
.bubble{
	position: relative;

	display: inline-block;
	width:50%;
	/*white-space: pre-wrap;*/
	min-height:400px;
	word-wrap: break-word;
	text-align:left;
	/*margin-left: 10px;*/
	font-family: 'Bad Script';
	font-size: 1.4em;
	font-weight: bolder;
	margin-left: 20px;

}
.person{
	float:left;

	padding:  10px 20px 10px 20px;
	width: 40%;
	/*margin: 0 20px 0 20px;*/
	display: inline-block;
	/*background-color: green;*/
	min-height: 300px;
	position: relative;
	right:100px;
	margin-left: 20px;
	/*margin:0px 70px 0px 20px;*/
}
.appr-profile{
	border-radius: 50%;
	/*max-width: 100%;*/
	max-width: 300px;
	/*margin-top: 50px;*/
}
.appr-desc{
	/*font-style: italic;*/
	font-family: 'Balsamiq Sans';
	font-weight: lighter;
}

@media screen and (max-width: 1200px) {
.bubble{
	display: block;
	width: 80%;
}
.qoutes{
	position: static !important;
	/*display: none;*/
	margin-bottom: 10px;
}
.person{
	display: block;
	width:100%;
	/*height:70px !important;*/
}
.appr-profile{
	max-width:40%;
}
}
