.twit-banner {
    width: 100%;
    padding: 8px 0 20px;
    background: white;
    position: relative;
    z-index: 1;
    margin-top: -8px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: border-box;
}


.twit-banner:after {
    -moz-border-bottom-colors: none;
    -moz-border-left-colors: none;
    -moz-border-right-colors: none;
    -moz-border-top-colors: none;
    border-color: white rgba(0,0,0,0) rgba(0,0,0,0);
    border-image: none;
    border-style: solid;
    border-width: 20px 20px 0;
    bottom: -20px;
    content: "";
    height: 0;
    left: 50.35%;
    margin-left: -24px;
    position: absolute;
    width: 0;
    z-index: 10;
}
#back-to-top{background-color:#656565;margin:0;position:fixed;bottom:0;right:20px;width:50px;height:40px;opacity:.5;text-align:center;color:#ffffff;border-radius:4px 4px 0 0;transition:background 0.15s ease-in-out 0s;}
#back-to-top:hover{background:#34D298;opacity:1;}



/* ==================================================
Footer
================================================== */
footer{width:100%;height:100%;color: rgba(0, 0, 0, 0.1);background-image: url("");background-repeat: no-repeat;position:relative;position:relative;z-index:-2;}
footer .footer-content{position:fixed;width:100%;z-index:-2;}
.layer{background-color: rgba(248, 247, 216, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;}
.sc_foot1{width:400px;}
.sc_foot2{width:600px;}
.sc_foot3{width:940px;}
.sc_foot4{width:1170px;}
.foot-sidebar div:nth-child(n + 5){display:none;}
#footer a,
#footer h4{color:#fff;}
/* Logo */
footer .footer-content .logo{margin:0 auto 20px;max-width:260px;text-align:center;position:relative;}
